.emotion-tester {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.tester-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  position: relative;
}

.back-button {
  position: absolute;
  left: 0;
  text-decoration: none;
  color: #8A2BE2;
  font-weight: 500;
  display: flex;
  align-items: center;
  padding: 5px 10px;
  border-radius: 4px;
  background-color: #f0e6ff;
  transition: all 0.2s ease;
}

.back-button:hover {
  background-color: #e6d6ff;
  transform: translateX(-5px);
}

.emotion-tester h2 {
  color: #8A2BE2;
  text-align: center;
  margin-bottom: 20px;
  width: 100%;
}

.tester-input {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.tester-input input {
  flex: 1;
  padding: 12px 15px;
  border: 2px solid #ddd;
  border-radius: 6px;
  font-size: 16px;
}

.tester-input button {
  padding: 0 20px;
  background: linear-gradient(90deg, #8A2BE2, #FF69B4);
  color: white;
  border: none;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.tester-input button:hover {
  opacity: 0.9;
  transform: translateY(-1px);
}

.tester-input button:disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none;
}

.loading {
  text-align: center;
  margin: 20px 0;
  color: #8A2BE2;
  font-weight: 600;
}

.results {
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 15px;
  margin-bottom: 20px;
}

.results h3 {
  color: #8A2BE2;
  margin-bottom: 15px;
  border-bottom: 1px solid #ddd;
  padding-bottom: 5px;
}

.result-section {
  margin-bottom: 15px;
  padding: 10px;
  background-color: white;
  border-radius: 6px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
}

.result-section h4 {
  color: #333;
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 1rem;
}

.highlight {
  background-color: #f0e6ff;
  color: #8A2BE2;
  padding: 2px 6px;
  border-radius: 4px;
  font-weight: 600;
}

.reply {
  font-style: italic;
  color: #555;
}

.test-examples {
  margin-top: 20px;
}

.test-examples h3 {
  color: #555;
  font-size: 1rem;
  margin-bottom: 10px;
}

.test-examples ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.test-examples li {
  background-color: #f0e6ff;
  padding: 8px 15px;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #8A2BE2;
}

.test-examples li:hover {
  background-color: #e6d6ff;
  transform: translateX(5px);
}
